.wrapper {
	max-width: 1280px;
	margin-right: auto;
	margin-left: auto;
	padding: 1.5em;
}

.masonry-wrapper {
	padding: 1.5em;
	max-width: 960px;
	margin-right: auto;
	margin-left: auto;
}

.masonry {
	display: grid;
	grid-template-columns: repeat(2, minmax(100px, 1fr));
	grid-gap: 10px;
	grid-auto-rows: 0;
}

@media only screen and (max-width: 1023px) and (min-width: 768px) {
	.masonry {
		grid-template-columns: repeat(3, minmax(100px, 1fr));
	}
}

@media only screen and (min-width: 1024px) {
	.masonry {
		grid-template-columns: repeat(4, minmax(100px, 1fr));
	}
}

.masonry-item, .masonry-content {
	border-radius: 4px;
	overflow: hidden;
}

.masonry-item {
	filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, .3));
	transition: filter .25s ease-in-out;
}

.masonry-item:hover {
	filter: drop-shadow(0px 5px 5px rgba(0, 0, 0, .3));
}

.masonry-content {
	overflow: hidden;
}
.masonry-content img {
width: 100%;
	border-radius: 4px;
}

.masonry-item {
	color: #111111;
	background-color: #f9f9f9;
}

.masonry-title {
	font-weight: 300;
	padding: .3rem 1.5rem;
}

.masonry-description {
	padding: .4rem 1.5rem;
	font-size: .75rem;
	border-top: 1px solid rgba(0, 0, 0, .05);
}
.masonry-footer {
	font-size: .75em;
	opacity: .25;
	text-align: center;
	padding-top: 3em;
	padding-bottom: 3em;
	margin-bottom: -1.5em;
	transition: opacity 1s ease-in-out;
}
.masonry-footer a {
	color: currentColor;
}
.masonry-footer:hover, .masonry-footer:active, .masonry-footer:focus {
	opacity: .75;
}